import React from "react";
import { NavLink } from "react-router-dom";

import { AppBar, Button } from "@material-ui/core";

import Logo from "../../../assets/images/Raven-Logo.png";

import "./style.css";

const Header = () => {
  const toggleMenu = () => {
    const nav = document.querySelector("nav");
    nav.classList.toggle("open");
  };

  return (
    <header>
      <div className="logo">
        <img src={Logo} alt="" />
        <span>Raven Livestream</span>
      </div>
      <nav className="nav">
        <NavLink to="/">Home</NavLink>
        <NavLink to="/settings">FAQs</NavLink>
        <NavLink to="/privacy-policy">Contact</NavLink>
        <NavLink to="/privacy-policy">Store</NavLink>
      </nav>
      <Button variant="contained">
        Login
        {/* <FontAwesomeIcon
              className="icon"
              icon={['fa', 'arrow-right']}
              size="2x"
            /> */}
      </Button>
      <div className="menu-toggle show-mobile" onClick={toggleMenu}></div>
    </header>
  );
};

export default Header;
