import React from "react";
import {
  Avatar,
  Box,
  Button,
  Card,
  Container,
  Grid,
  Typography,
} from "@material-ui/core";

import BouncingPhone from "../../../../assets/images/iPhone 13.png";
import MonetizeIllustration from "../../../../assets/images/Frame 2524.png";
import nestImage from "../../../../assets/images/nest-image.png";

import WithdrawIllustration from "../../../../assets/images/WithdrawIllustration.png";
import HostingIllustration from "../../../../assets/images/HostingIllustration.png";
import CryptoIllustration from "../../../../assets/images/CryptoIllustration.png";

import "./style.scss";
import Header from "../../Header/Header";

const PossibleEvents = [
  {
    title: "Concerts",
    description: "Naya is streaming her concert “Happy & Alive",
  },
  {
    title: "Talkshow",
    description: "Jennifer Adelade is speaking, Listen in.",
  },
  {
    title: "Masterclass",
    description: "Elsa just started  an event “Remote work tips 101 ",
  },
  {
    title: "Comedy",
    description: "Leah mavrick just created an event “Laugh House ",
  },
  {
    title: "Fundraiser",
    description: "Kwame has just raised $3,554, Join the fundraiser",
  },
  {
    title: "Podcast",
    description: "Ari is live, Listen in.",
  },
  {
    title: "Parties",
    description: "Tokunbo just started a party, Join in!",
  },
  {
    title: "Movies",
    description: "Kehinde just publish a movie 'Dynamite'",
  },
  {
    title: "Games",
    description: "Toby & Nana are playing “Fortnite”.  Join now!",
  },
];

const Home = () => {
  return (
    <main id="home">
      <section className="top-section">
        <div className="discoLines"></div>

        <Header />

        <div className="hero">
          <div className="">
            <h2>Create, Earn and Connect</h2>
            <p>
              Raven is a live-streaming and social networking app helping
              African creators earn globally
            </p>

            {/* Download Options */}
            <div className="download-platforms">
              <a href="https://apps.apple.com/ng/app/raven-livestream/id1541951758">
                {/* <img src={AppleDownload} alt="" className="button" /> */}
              </a>
              <a href="" className="button">
                <img
                  // src={GoogleDownload}
                  alt="https://play.google.com/store/apps/details?id=com.ravenlivestream.raven"
                />
              </a>
            </div>
          </div>

          <div className="imgContainer">
            <img src={BouncingPhone} alt="" />
          </div>
        </div>
      </section>

      <section className="future-talk">
        <Container maxWidth="false">
          <Typography variant="h4">
            Welcome to the future of entertainment
          </Typography>
          <Typography variant="body2">
            You now have the freedom to create events right in the moment.
            Instantaneously host live events, publish premium content, share
            ideas with the tap of a finger.
          </Typography>
        </Container>
      </section>

      <section className="section events">
        <Container maxWidth="false">
          <Typography variant="h4">
            Enjoy events from the comfort of your home
          </Typography>

          <Typography variant="body2">
            You have access to millions of live events and premium content
            happening worldwide in real-time. Buy tickets and tune in to your
            favorite events
          </Typography>

          <Box>
            <Grid container spacing={3} className="cards">
              {PossibleEvents.map((PossibleEvent, i) => (
                <Grid key={i} item xs={12} md={4} lg={3}>
                  <Card className="card">
                    <Avatar />
                    <div className="">
                      <Typography variant="h6">
                        {PossibleEvent.title}
                      </Typography>
                      <Typography variant="body2">
                        {PossibleEvent.description}
                      </Typography>
                    </div>
                  </Card>
                </Grid>
              ))}
            </Grid>
          </Box>
        </Container>
      </section>

      <section className="features-explained">
        <Container maxWidth="false">
          <Grid container spacing={5} alignItems={"center"}>
            <Grid item md="6">
              <img
                src={HostingIllustration}
                alt=""
                style={{ height: "100%", width: "100%" }}
              />
            </Grid>
            <Grid item md="6">
              <Typography variant="h3">
                An entirely new approach to entertainment.
              </Typography>
              <Typography variant="body2" p>
                Host and Monetize Live Events in less time, with NO barriers,
                all on your mobile device.
              </Typography>
            </Grid>
          </Grid>
        </Container>

        <Container maxWidth="false">
          <Grid container spacing={5} alignItems={"center"}>
            <Grid item md="6">
              <Typography variant="h3">
                <span style={{ color: "#3f50b5" }}> Withdraw earnings </span>
                directly to your bank.
              </Typography>
              <Typography variant="body2" p>
                Withdraw ticket sales, donations and crypto payments into your
                bank account seamlessly in less than 24 hours.
              </Typography>
            </Grid>
            <Grid item md="6">
              <img
                src={WithdrawIllustration}
                alt=""
                style={{ height: "100%", width: "100%" }}
              />
            </Grid>
          </Grid>
        </Container>
      </section>

      <section>
        <Container>
          <Typography variant="h4">
            We are
            <span style={{ color: "#3f50b5" }}>democratizing content</span>
          </Typography>
          <Typography variant="body2">
            Publishing and distributing content just got easier.
          </Typography>
        </Container>
      </section>

      <section>
        <Container maxWidth="false">
          <Grid container spacing={5} alignItems={"center"}>
            <Grid item md="6">
              <Typography variant="h3">
                <span style={{ color: "#3f50b5" }}>
                  Monetize digital products
                </span>{" "}
                from anywhere, anytime!
              </Typography>
              <Typography variant="body2" p>
                Take advantage of our Play feature. You can automatically
                re-publish and monetize your video and audio content like
                YouTube videos, Anchor/Spotify content, and music.
              </Typography>
            </Grid>
            <Grid item md="6">
              <img
                src={MonetizeIllustration}
                alt=""
                style={{ height: "100%", width: "100%" }}
              />
            </Grid>
          </Grid>
        </Container>
      </section>

      <section>
        <Container maxWidth="false">
          <Grid container spacing={5} alignItems={"center"}>
            <Grid item md="6">
              <img
                src={CryptoIllustration}
                alt=""
                style={{ height: "100%", width: "100%" }}
              />
            </Grid>
            <Grid item md="6">
              <Typography variant="h3">Cryptocurrency payments</Typography>
              <Typography variant="body2" p>
                You can buy tickets and make payments in USD and receive tips in
                Solana no matter where you are in the world. You earn 90% of all
                donations in USD and keep 100% of all Solana Tips.
              </Typography>
              <Button variant="contained" color="primary">
                Start Earning
              </Button>
            </Grid>
          </Grid>
        </Container>
      </section>

      <section className="nest-platform">
        <Container justifyContents="center">
          <img src={nestImage} alt="" />
          <Typography variant="h4">Join The Nest</Typography>
          <Typography variant="body2">
            Share ideas, meet and interact with cool people from all over the
            world. Nest is a community for creators that truly believes in free
            speech. . Drop a “Twig” today!
          </Typography>
        </Container>
      </section>
    </main>
  );
};

export default Home;
